import React from 'react';

import quehack from '../../img/quehack-top.png';

function WorkRow(props){
    console.log(props);
    var odd = (
        <div className="container">
            <div className="left"><img src={props.img} className="work_img" /></div>
                <div className="right">
                    <div className="text">
                        <div className="name">{props.name}</div>
                        <div className="date">{props.date}</div>
                        <div className="position">{props.position}</div>
                        <div className="tools"><span>Tools: </span>{props.tools}</div>
                        <div className="desc"><span>Objective: </span>{props.desc}</div>
                        <div className="role"><span>Role: </span>{props.role}</div>
                    </div>
            </div>
        </div>
    );

    var even = (
        <div className="container">
            <div className="left">
                <div className="text">
                    <div className="name">{props.name}</div>
                    <div className="date">{props.date}</div>
                    <div className="position">{props.position}</div>
                    <div className="tools"><span>Tools: </span>{props.tools}</div>
                    <div className="desc"><span>Objective: </span>{props.desc}</div>
                    <div className="role"><span>Role: </span>{props.role}</div>  
                </div>        
            </div>
            <div className="right">
                <img src={props.img} className="work_img" />
            </div>
        </div>
    )

    return(
        <div className="work_row">
            {(props.num%2 === 0) ? odd : even}
            {(props.num !== 5) ? <div style={{marginBottom:'30px', width:'100%'}}>   </div> : undefined}
        </div>
    )
}

export default WorkRow